import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('tested')
export class tested extends Component {
    test = null
    start() {
        console.log('111111111111111');
        const test = this.encode('abcd','abcd')
        setTimeout(() => {
            console.log('打印');
            console.log(this.decode(test,'abcd'));
        }, 2000);
    }
    // 加密函数
    encode(input, key) {
    let buf:any = unescape(encodeURIComponent(input))
    let keys = unescape(encodeURIComponent(key))
    let encoded = ''
    for (const i in buf) {
        encoded += String.fromCharCode(buf.charCodeAt(i) ^ keys.charCodeAt(i % keys.length) ^ (255 & i))
    }
    return btoa(encoded)
}

// 解密函数
    decode(input, key) {
    let buf:any = atob(input)
    let keys = unescape(encodeURIComponent(key))
    let decoded = ''
    for (const i in buf) {
        decoded += String.fromCharCode((255 & i) ^ buf.charCodeAt(i) ^ keys.charCodeAt(i % keys.length))
    }
    return decodeURIComponent(escape(decoded))
}

    update(deltaTime: number) {
        
    }
}

